import React from 'react';
// import { Button } from 'react-bootstrap'
export default function Post({ post, deletePost }) {

  return (
    <div className="list-group">
        <a href="#" className="list-group-item">
            <h2>{post.title}</h2>
            <img style={{"width":"100%", "height": "230px"}} src={post.url}></img>
            <center>
            <blockquote>
                <p>{post.content}</p>
                  <small> on July 8, 2014</small>
                  <br/>
                  <button className="btn btn-primary btn-sm col-sm-3" type="button" onClick={() => console.log("Edit !!")} >Edit</button>
                  <button className="btn btn-danger btn-sm pull-right" type="button" onClick={() => deletePost(post.id)}>Delete</button>
              </blockquote>
            <hr />
          </center>
        </a>
    </div>
  );
}



