import React, { Component } from 'react';
import ListPost from './ListPost'
import {connect} from 'react-redux'
import { addPost, sortPost, deletePost } from './actions'
// import { Button, Grid, Row, Col, Glyphicon } from 'react-bootstrap'

class App extends Component {

  componentDidMount(){
    this.props.addPost({
      title: "CAT",
      url: "http://www.cats.org.uk/uploads/branches/1/environment-faqs.jpg",
      "content": "Plus, you can now set your own date range for dataselect from previous month or week or set your own custom dates.Raw denim you probably haven't heard of them jean shorts Austin. Nesciunt tofu stumptown aliqua, retro synth master cleanse. Mustache cliche tempor, williamsburg carles vegan helvetica. Reprehenderit butcher retro keffiyeh dreamcatcher synth. Cosby sweater eu banh mi, qui irure terry richardson ex squid. Aliquip placeat salvia cillum iphone. Seitan aliquip quis cardigan american apparel, butcher voluptate nisi qui."
    })

    // this.props.addPost({
    //   title: "AAT",
    //   url: "https://www.w3schools.com/css/paris.jpg",
    //   "content": "Plus, you can now set your own date range for dataselect from previous month or week or set your own custom dates."
    // })
  }

  render() {
    return (
      <div>
      <center >
        <h1>Welcome to ABC Blog Taking</h1>
        <hr className="small"/>
      </center>
        <div className="container">
        <div className="row">
            <center>
              <p>Sort By</p>
              {' '}
              {' '}
              <a href="" >Name</a>
              {'   |   '}
              <a href="" >Lasted</a>
              {' '}
              <br/>
            </center>
            <br/>
            {' '}

            <center className="col-sm-8 col-md-offset-2">
              <button className="btn btn-success btn-md btn-block" type="button" onClick={() => console.log("ADD!!")}>Add New Content</button>
              <ListPost posts={this.props.posts} deletePost={this.props.deletePost} ></ListPost>
            </center>
          </div>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    posts: state.posts
  }
}

export default connect(mapStateToProps, { addPost, sortPost, deletePost })(App);



